














import Task from "../models/Task";
import Vue from "vue";

export default Vue.extend({
  name: "Create",
  data() {
    return {
      taskName: "",
    };
  },
  methods: {
    createTask(): void {
      if (this.taskName !== "") {
        let newTask = new Task(this.taskName);
        this.$store.commit("setTask", newTask);
        this.taskName = "";
        this.$toast.add({
          severity: "success",
          summary: "Success Message",
          detail: "Task added",
          life: 3000,
        });
      }
    },
  },
});
